 Funktionen beginnt Baeck eine umfangreiche Lehrttigkeit an verschiedenen Universitten in Europa und den USA. Leo Baeck stirbt am 2. November 1956 in London\nQuelle:http://www.ghetto-theresienstadt.info/pages/b/baeckl.htm',
